body{
  background: url("images/main_background.jpg");
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: rgb(255, 255, 255);
  letter-spacing: 3px;
}

*{
  text-transform: uppercase;
}

a{
  text-decoration: none;
  color: white;
  cursor: pointer;
}

.buttons{
  display: flex;
  width: 80%;
  justify-content: space-around;
  font-size: 20px;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.arrow{
  background: rgba(255, 255, 255, 0.35);
  height: 40px;
  width: 40px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  outline: none;
  border: none;
  border-radius: 50px;
  cursor: pointer;
}

.arrow:hover{
  background: rgba(255, 255, 255, 0.75);
}

.nav_button > a:hover{
  color: rgba(255, 255, 255, 0.75);
}

.left_arrow{
  left: 10px;
}

.right_arrow{
  right: 10px;
}

.title{
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  width:fit-content;
  transform: translate(-50%, -50%);
  font-size: 50px;
  font-weight: 600;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 0 20px;
}

.bar{
  background: rgba(0, 0, 0, 0.5);
  height: 40px;
}

.buttons > span > a{
  text-transform: uppercase;
  font-weight: 600;
  color: white;
}

.nav_button{
  outline: none;
  border: none;
  border-left: 1px solid white;
  cursor: pointer;
  padding-left: 15px;
  background: none;
  font-size: 20px;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.nav_button:hover{
  color: rgba(255, 255, 255, 0.75);
}

html, body, #root, .App, .container, .front{
  width: 100%;
  height: 100%;
}

.info > a:hover{
  color: rgba(255, 255, 255, 0.5);
}

.front{
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
}

.section{
  height: 75%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.feed{
  width: 80%;
  height: 100%;
}

.heading{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 15%;
  position: relative;
}

.viewmore{
  position: absolute;
  right: 100px;
  top: 15px;
}

.viewmore > a { 
  font-size: 12px;
  color: #ffffff;
}
.viewmore > a > span{
  background: linear-gradient(to right, #fd7014, #e26a1a);
  padding: 7px;
}

.viewmore > a > span:hover{
  color: #fd7014;
  background: white;
}

.viewmore > a > span:nth-child(1){
  border-top-left-radius: 10px ;
  /* border-bottom-left-radius: 15px; */
}

.viewmore > a > span:nth-child(2){
  background: white;
  color: #fd7014;
  /* border-top-right-radius: 15px ; */
  border-bottom-right-radius: 10px;
}

.heading > span{
  margin-top: 10px;
  font-size: 20px;
}

.food{
  height: 85%;
  display: flex;
  justify-content: space-around;
}

.img{
  height: 250px;
  width: 250px;
  border-radius: 50%;
  background: rgb(0, 0, 0);
  margin-top: 25px;
  box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.8);
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
}

.img_fit{
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.img_fit:hover{
  opacity: 0.75;
}

.block{
  height: 100%;
  width: 300px;
  position: relative;
}

.info{
  text-align: center;
  margin-top: 25px;
  font-weight: 100;
}

.button_order{
  margin-top: 15px;
  position: relative;
}

.button_order > button{
  outline: none;
  color: white;
  border: none;
  cursor: pointer;
  padding: 0 15px;
  background: none;
  font-size: 20px;
}

.button_order > button:hover{
  color: rgba(255, 255, 255, 0.5);
}

.button_order .minus{
  position: absolute;
  left: 25px;
}

.button_order .plus{
  position: absolute;
  right: 25px;
}

.button_order span{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 20px;
}